import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
@Component({
  selector: 'app-userchat',
  templateUrl: './userchat.component.html',
  styleUrls: ['./userchat.component.css']
})
export class UserchatComponent implements OnInit {
  name: any = {
    Name : "",
    profile_pic: ""
  };
  mesg;
  constructor(private ms:MurmurService) { }
  
  ngOnInit() {
    setInterval(() => {
      if(this.name != this.ms.chatName)
        this.name = this.ms.chatName;

      if(Object.keys(this.ms.chatName).length == 0)
      {
        this.name = {
          Name : "",
          profile_pic: "assets/img/pic1.png",
        }
      }
    }, 500);
  }

  send_whisper()
  {
    if(this.name.Name != "")
    {
      if(this.mesg != undefined )
        this.ms.whisper(this.name.account_name, this.mesg).then(res => {
          console.log(res)
          this.mesg = undefined;
        })
      else
        alert("Enter the message!")
    }
    else
    {
      alert("Please choose any receiver account")
    }
  }

}
