import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor( private ms: MurmurService, private router:Router ) { }

  ngOnInit() {
  }

  Scat_login()
  {
    this.ms.Scater_login().then(res => {
      if(res)
      {
        let set_int = setInterval(()=>{
          if( localStorage.getItem(this.ms.Scatter_account_name) != null )
          {
            // console.log("Login Acc "+localStorage.getItem(this.ms.Scatter_account_name))
            clearInterval(set_int);
            this.router.navigate(['/']);
          }
          else
            this.ms.Scater_login()            
        }, 50)
      }
    })
  }

  PriKey_login()
  { 
    var Pri_key= ((document.getElementById("privateKey") as HTMLInputElement).value)
    var Acc_name= ((document.getElementById("accountname") as HTMLInputElement).value)
    if(Acc_name != '')
    {
      this.ms.isPri_Valid(Pri_key).then(res => {
        if(res)
        {
          this.ms.PKey_login(Acc_name, Pri_key).then(res => {
            let set_int = setInterval(()=>{
              if( localStorage.getItem(this.ms.Scatter_account_name) != null )
              {
                clearInterval(set_int);
                // console.log("Acc "+ localStorage.getItem(this.ms.Scatter_account_name))
                this.router.navigate(['/']);
              }           
            }, 50)
            
          })
        }
        else
        {
          alert("Enter the valid private key")
        }
      })
    }
    else
      alert("Please Enter the account name");    
  }
}

