import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-sharepost',
  templateUrl: './sharepost.component.html',
  styleUrls: ['./sharepost.component.css']
})
export class SharepostComponent implements OnInit {

  Murmur_Posts: any = [];
  yell:any = '';
  My_Profile: any = {};

  constructor( private ms: MurmurService, private router:Router ) { }

  ngOnInit() {

    this.ms.getAccountProfile().then(res => {
      this.My_Profile = res[0];
      // console.log(res)
    })   

    this.ms.getCommentData().then(res => {
      if(res['id'] != undefined)
      {
        if(res['type'] == 'murmur')
        {
          console.log("mur")
          this.ms.getMurmurByIndex(res['id']).then(mur => {
            this.Murmur_Posts.push(mur);
          })
        }
        else
        {
          console.log("mymur")
          this.ms.getMyMurmurByIndex(res['id']).then(mur => {
            this.Murmur_Posts.push(mur);
          })
        }
      }
      else
      {
        this.router.navigate(['/feed']);
      }
    })    
  }

  cmt_ID(id)
  {
    this.ms.getCommentData().then(res => {
      this.ms.setCommentData(id, res['type'])
    })
  }

  snoop(id)
  {
    this.ms.getCommentData().then(res => {
      this.ms.Snoop(res['id'], res['type']).then(res => {
        console.log(res)
      })
    })
  }

  post_yell()
  {
    // console.log(this.Murmur_Posts[0].murmur.murmur_id)
    this.ms.yell(this.Murmur_Posts[0].murmur.murmur_id, this.yell).then(res => {
      console.log(res)
      this.router.navigate(['/feed']);
    })

    this.ms.getCommentData().then(res => {
      let mur = [];
      if(res['id'] != undefined)
      {
        if(res['type'] == 'murmur')
        {
          // console.log("mur")
          this.ms.getMurmurByIndex(res['id']).then(resp => {
            mur.push(resp)
            console.log(mur)
            this.ms.yell(mur[0].murmur.murmur_id,this.yell).then(result => {
              console.log(result);
              this.router.navigate(['/feed']);
            })            
          })
        }
        else
        {
          // console.log("mymur")
          this.ms.getMyMurmurByIndex(res['id']).then(resp => {
            mur.push(resp)
            console.log(mur)
            this.ms.yell(mur[0].murmur.murmur_id,this.yell).then(result => {
              console.log(result);
              this.router.navigate(['/feed']);
            })
          })
        }
      }
      else
      {
        this.router.navigate(['/feed']);
      }
    })
  }

}