import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-userprofilelist',
  templateUrl: './userprofilelist.component.html',
  styleUrls: ['./userprofilelist.component.css']
})
export class UserprofilelistComponent implements OnInit {

  Followers_Count: Number;
  Following_count: Number;
  Murmur_count: Number;
  My_Profile: any = {};
  name: any = '';

  constructor( private ms: MurmurService ) { }

  ngOnInit() {
    setInterval(() => {
      this.ms.getOtherUserName().then(res => 
      {
        if(this.name != res)
        {
          this.name = res;
          if(res == undefined)
          {
            this.ms.getAccountProfile().then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
        
            this.ms.getFollwersCount(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
              this.Followers_Count = res;
              // console.log(res)
            })
        
            this.ms.getFollwingCount(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
              this.Following_count = res;
              // console.log(res)
            })
        
            this.ms.getMurmursCount(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
              this.Murmur_count = res;
            })
          }
          else
          {
            this.ms.getOthersProfile(res).then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
        
            this.ms.getFollwersCount(res).then(res => {
              this.Followers_Count = res;
              // console.log(res)
            })
        
            this.ms.getFollwingCount(res).then(res => {
              this.Following_count = res;
              // console.log(res)
            })
        
            this.ms.getMurmursCount(res).then(res => {
              this.Murmur_count = res;
            })
          }
        }
      })
    }, 50)
  }

  logout()
  {
    if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 1)
      this.ms.Scater_logout().then().catch(e => {
        console.log("error")
        console.log(e)
      })
    else
      this.ms.PKey_logout().then().catch(e => {
        console.log("error")
        console.log(e)
      })
  }
}
