import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { Routes, RouterModule } from '@angular/router';
import { NgxSpinnerModule } from 'ngx-spinner';
import { ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { MainappComponent } from './mainapp/mainapp.component';
import { MainapponeComponent } from './mainappone/mainappone.component';
import { MainapptwoComponent } from './mainapptwo/mainapptwo.component';
import { WisperchatlistComponent } from './wisperchatlist/wisperchatlist.component';
import { UserprofilelistComponent } from './userprofilelist/userprofilelist.component';
import { SharepostComponent } from './sharepost/sharepost.component';
import { CommentpostComponent } from './commentpost/commentpost.component';
import { FeedComponent } from './feed/feed.component';
import { UserchatComponent } from './userchat/userchat.component';
import { UserpersonalfeedComponent } from './userpersonalfeed/userpersonalfeed.component';
import { OtheruserpersonalfeedComponent } from './otheruserpersonalfeed/otheruserpersonalfeed.component';
import { PostmurmurComponent } from './postmurmur/postmurmur.component';
import { EditmyprofileComponent } from './editmyprofile/editmyprofile.component';
import { LogincontentComponent } from './logincontent/logincontent.component';
// import { MurmurdsbpnlComponent } from './murmurdsbpnl/murmurdsbpnl.component';
// import { MurmurdsbgblComponent } from './murmurdsbgbl/murmurdsbgbl.component';

import { MurmurService } from './murmur.service';
import { AuthGuard } from './AuthGuard/auth.guard';

const routes: Routes = [
  {path:'', component:FeedComponent, canActivate: [AuthGuard]},
  {path:'', component:UserprofilelistComponent, outlet:'feed', canActivate: [AuthGuard]},
  {path:'login', component:LogincontentComponent},
  {path:'login', component:LoginComponent, outlet:'feed'},
  {path:'feed', component:FeedComponent, canActivate: [AuthGuard]},
  {path:'userprofilelist', component:UserprofilelistComponent, outlet:'feed', canActivate: [AuthGuard]},
  {path:'user', component:UserprofilelistComponent, outlet:'feed', canActivate: [AuthGuard]},
  {path:'chat', component:WisperchatlistComponent, outlet:'feed', canActivate: [AuthGuard]},
  {path:'userchat', component:UserchatComponent, canActivate: [AuthGuard]},
  {path:'share', component:SharepostComponent, canActivate: [AuthGuard]},
  {path:'comment', component:CommentpostComponent, canActivate: [AuthGuard]},
  {path:'create', component:PostmurmurComponent, canActivate: [AuthGuard]},
  {path:'userpost', component:UserpersonalfeedComponent, canActivate: [AuthGuard]},
  {path:'editprofile', component:EditmyprofileComponent, canActivate: [AuthGuard]},
];

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    MainappComponent,
    MainapponeComponent,
    MainapptwoComponent,
    WisperchatlistComponent,
    UserprofilelistComponent,
    SharepostComponent,
    CommentpostComponent,
    FeedComponent,
    UserchatComponent,
    UserpersonalfeedComponent,
    OtheruserpersonalfeedComponent,
    PostmurmurComponent,
    EditmyprofileComponent,
    LogincontentComponent,
    // MurmurdsbpnlComponent,
    // MurmurdsbgblComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    NgxSpinnerModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule
  ],
  providers: [MurmurService],
  bootstrap: [AppComponent]
})
export class AppModule { }
