import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-userpersonalfeed',
  templateUrl: './userpersonalfeed.component.html',
  styleUrls: ['./userpersonalfeed.component.css']
})
export class UserpersonalfeedComponent implements OnInit {

  // Murmur_Posts: any = [];
  My_Murmur_Posts: any = [];
  My_Profile: any = {};
  name: any = '';

  constructor(private ms: MurmurService) { }

  ngOnInit() {

    setInterval(() => {
      this.ms.getOtherUserName().then(res => 
      {
        if(this.name != res)
        {
          this.name = res;
          if(res == undefined)
          {
            this.ms.getAccountProfile().then(res => {
              this.My_Profile = res[0];
              // console.log(res[0])
            })
            
            this.ms.getMyMurmurs(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(resp => {
              this.My_Murmur_Posts = resp
              // console.log(res)
            })
          }
          else
          {
            this.ms.getOthersProfile(res).then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
            
            this.ms.getMyMurmurs(res).then(resp => {
              this.My_Murmur_Posts = resp
              // console.log(res)
            })
          }
        }
      })
    }, 50)
  }

  cmt_ID(id)
  {
    this.ms.setCommentID(id)
    this.ms.setCommentData(id, 'mymurmur')
  }

  snoop(id)
  {
    this.ms.Snoop(id, 'mymurmur').then(res => {
      console.log(res)
      this.My_Murmur_Posts = [];
      this.ms.getMyMurmurs(this.name).then(res => {
        this.My_Murmur_Posts = res
      })
    })
  }

}
