  // Get calls

  public async getMurmurs(): Promise<object> {
    return new Promise((resolve, reject) =>{

      this.spin.show();
      this.http
        .get<{ message: string; posts: any }>(
          "http://murmurjapi.wandx.co/api/murmurTimeline?account_name=" + localStorage.getItem(this.Scatter_account_name).split('-')[0]
        )
        .subscribe(postData => {
           var mur = Object.values(postData)[1]
          this.Murmur_Posts = []
          var my = [];
          for(var i=0; i<mur.length; i++)
          {
            if(mur[i].action_type == 'murmur')
            {
              let mur_url = mur[i].murmur.attached_url
              if(mur_url.split('.').length == 1 && mur_url !== '')
              mur[i].murmur.attached_url = 'https://ipfs.io/ipfs/' + mur_url;

              my.push({
                id : mur[i]._id,
                action_type: mur[i].action_type,
                account_name: mur[i].account_name,
                Acc_id: '@'+mur[i].account_name,
                image: this.image,
                murmur: {
                  id : mur[i].murmur._id,
                  murmur_id: mur[i].murmur.murmur_id,
                  message: mur[i].murmur.message,
                  attached_url: mur[i].murmur.attached_url,
                  visibility: mur[i].murmur.visibility,
                  yell_count: mur[i].murmur.yell_count,
                  comment_count: mur[i].murmur.comment_count,
                  snoop_count: mur[i].murmur.snoop_count
                },
                createdAt: mur[i].createdAt,
                yell: {}
              })
            }
            else
            {
              let mur_url = mur[i].yell.murmur.attached_url
              if(mur_url.split('.').length == 1 && mur_url !== '')
                mur[i].yell.murmur.attached_url = 'https://ipfs.io/ipfs/' + mur_url;

                my.push({
                id : mur[i]._id,
                action_type: mur[i].action_type,
                account_name: mur[i].account_name,
                Acc_id: '@'+mur[i].account_name,
                image: this.image,
                yell: {
                  id : mur[i].yell._id,
                  yell_id: mur[i].yell.yell_id,
                  extra_comment: mur[i].yell.extra_comment,
                  murmur_id: mur[i].yell.murmur.murmur_id ,
                  mur_account_name: mur[i].yell.murmur.account_name,
                  mur_acc_id: '@'+mur[i].yell.murmur.account_name,
                  mur_acc_image: this.image,
                  message: mur[i].yell.murmur.message,
                  attached_url: mur[i].yell.murmur.attached_url,
                  visibility: mur[i].yell.visibility,
                  comment_count: mur[i].yell.comment_count,
                  snoop_count: mur[i].yell.snoop_count,
                },
                createdAt: mur[i].createdAt,
              })
            }
          }

          // console.log(my)

          for(let i=0; i<my.length;i++)
          {
            this.getOthersProfile(my[i].account_name).then(res => {
              // console.log(res)
              my[i].account_name = res[0].account_name;
              my[i].image = res[0].profile_pic;
              if(my[i].action_type == 'yell')
              {
                this.getOthersProfile(my[i].yell.mur_account_name).then(res => {
                  // console.log(res)
                  my[i].yell.mur_account_name = res[0].account_name;
                  my[i].yell.mur_acc_image = res[0].profile_pic;
                })
                // console.log(my)
              }
            })
            // console.log(my)
          }

          this.Murmur_Posts = my;
          resolve(this.Murmur_Posts);
          this.spin.hide();
        })
    })as Promise<object>;
  }

  public async getMyMurmurs(acc_name): Promise<object> {
    return new Promise((resolve, reject) =>{
      let temparray = [];
      let name, img;
      this.spin.show();
      this.http
        .get<{ message: string; posts: any }>(
          "http://murmurjapi.wandx.co/api/getMurmursByAccountName?account_name=" + acc_name
        )
        .subscribe( postData => {
          var mur = Object.values(postData)[1]
          for(var i=0; i<mur.length; i++)
          {
            let mur_url = mur[i].attached_url
            if(mur_url.split('.').length == 1 && mur_url !== '')
              mur[i].attached_url = 'https://ipfs.io/ipfs/' + mur_url;

            if(this.Other_User_name)
            {
              name = this.Others_Acc_Profile[0].account_name
              img = this.Others_Acc_Profile[0].profile_pic
            }
            else
            {
              name = this.Acc_Profile[0].account_name
              img = this.Acc_Profile[0].profile_pic
            }

            temparray.push({
              id : mur[i]._id,
              action_type: 'murmur',
              account_name: name,
              Acc_id: '@'+mur[i].account_name,
              image: img,
              murmur: {
                murmur_id: mur[i].murmur_id,
                message: mur[i].message,
                attached_url: mur[i].attached_url,
                visibility: mur[i].visibility,
                yell_count: mur[i].yell_count,
                comment_count: mur[i].comment_count,
                snoop_count: mur[i].snoop_count,
              },
              createdAt: mur[i].createdAt,
            })
          }
          this.My_Murmur_Posts = temparray.reverse()
          console.log(this.My_Murmur_Posts)
          resolve(this.My_Murmur_Posts);
          this.spin.hide();
        })
    })as Promise<object>;
  }

