  public async whisper(to_account_name, message): Promise<object> {
    return new Promise((resolve, reject) =>{
      if(localStorage.getItem(this.Scatter_account_name).split('-').length == 2)
      {
        this.AccToPub(localStorage.getItem(this.Scatter_account_name).split('-')[0]).then(myPub => {

          this.AccToPub(to_account_name).then(toPub => {
            const { ecc } = Eos.modules
            let encrypted_message = ecc.Aes.encrypt(localStorage.getItem(this.Scatter_account_name).split('-')[1], toPub, message);
            encrypted_message.nonce = encrypted_message.nonce.toString();
            encrypted_message.message = encrypted_message.message.toString("binary");
            encrypted_message = JSON.stringify(encrypted_message);

            console.log(localStorage.getItem(this.Scatter_account_name).split('-')[0], to_account_name, myPub, toPub, encrypted_message)

            if(localStorage.getItem(this.Scatter_account_name).split('-').length == 2)
            {
              this.SetConfig().then(eos => {
                eos.transaction('murmurdappco', contract => {
                  contract.whisper(localStorage.getItem(this.Scatter_account_name).split('-')[0], to_account_name, myPub, toPub, encrypted_message);              
                }, this.config).then(trx => {
                  // console.log(`Transaction ID: ${trx.transaction_id}`);
                  this.Murmur_Posts.length = 0
                  resolve(trx.transaction_id)
                }).catch(error => {
                  console.error(error);
                  reject(error);
                });
              })
            }
          })
        })
      }
      else
      {
        alert("Its only working in Privatekey login")
      }
       
    })as Promise<object>;
  }
  
