import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { HttpClient } from '@angular/common/http';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

@Component({
  selector: 'app-postmurmur',
  templateUrl: './postmurmur.component.html',
  styleUrls: ['./postmurmur.component.css']
})
export class PostmurmurComponent implements OnInit {

  murmur: any = '';
  public selectedFile: any = {};
  File_select: boolean;
  My_Profile: any = {};

  constructor( private ms: MurmurService, private router:Router, private http:HttpClient, private spin : NgxSpinnerService ) { }

  ngOnInit() {

    this.ms.getAccountProfile().then(res => {
      this.My_Profile = res[0];
      console.log(res)
    })
  }

  onFileChanged(event){
    this.selectedFile = {};
    this.selectedFile = event.target.files[0];
    this.File_select = true;
    // console.log(this.selectedFile)
  }

  file_clear()
  {
    this.selectedFile = {};
    this.File_select = false;
    console.log("Cleared")
  }

  post()
  {
    if(this.File_select && this.murmur != '')
    {
      this.spin.show()
      let fd = new FormData();
      fd.append('path', this.selectedFile);
      this.http.post("https://ipfs.infura.io:5001/api/v0/add?stream-channels=true",fd).subscribe((r)=>{
        console.log(r['Hash']);
        if(this.murmur == '') this.murmur = "";
        this.ms.postMurmur(this.murmur,r['Hash']).then(res => { 
          console.log(res)
          this.spin.hide()
          this.router.navigate(['/feed']);
        }).catch(err =>  {
          console.log(err)
          this.spin.hide()
        })
      })
    }
    else
    {
      if(this.murmur != '')
        this.ms.postMurmur(this.murmur,'').then(res => { 
          console.log(res)
          this.router.navigate(['/feed']);
        })
      else
        alert("Please fill Murmur field")
    }

    // this.ms.follow(this.murmur).then(res => { 
    //   console.log(res)
    //   this.router.navigate(['/feed']);
    // }).catch(err =>  {
    //   console.log(err)
    //   this.spin.hide()
    // })

  }
}
