import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-editmyprofile',
  templateUrl: './editmyprofile.component.html',
  styleUrls: ['./editmyprofile.component.css']
})
export class EditmyprofileComponent implements OnInit {

  public selectedFile: any = {};
  name: any;
  My_Profile: any = {};

  constructor(private ms: MurmurService, private router:Router) { }

  ngOnInit() {
    if(localStorage.getItem(this.ms.Scatter_account_name) != null)
    {
      this.name = localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]

      this.ms.getAccountProfile().then(res => {
        this.My_Profile = res[0];
        console.log(res)
      })
      // console.log(this.name)
    }
    else
      this.router.navigate(['/']);

    
  }

  onFileChanged(event){
    this.selectedFile = {};
    this.selectedFile = event.target.files[0];
  }

  update_profile()
  {
    var accname= ((document.getElementById("inputEmail3") as HTMLInputElement).value);
    var bio= ((document.getElementById("exampleFormControlTextarea1") as HTMLInputElement).value);
    var date= ((document.getElementById("date1") as HTMLInputElement).value);
    var gender = (document.querySelector('input[name="gridRadios"]:checked')as HTMLInputElement).value;
    var location= ((document.getElementById("location") as HTMLInputElement).value);

    let fd = new FormData();
    fd.append('account_name', this.name);
    fd.append('name', accname);
    fd.append('dob', date);
    fd.append('location', location);
    fd.append('bio', bio);
    fd.append('sex', gender);
    fd.append('profile_pic', this.selectedFile);

    console.log(this.name)

    this.ms.Post_user_profile(fd).then(res => {
      if(res)
        window.location.reload()
    })
  }

}
