import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-wisperchatlist',
  templateUrl: './wisperchatlist.component.html',
  styleUrls: ['./wisperchatlist.component.css']
})
export class WisperchatlistComponent implements OnInit {

  ChatUserList: any = [
    {
      "Name": "Bala Vignesh",
      "Time": "06:45PM",
      "msg": "Haliaeetus leucocephalus",
      "profile_pic": "assets/img/pic1.png",
      "account_name": "chainflux123"
    },
    {
      "Name": "Sixface Aru",
      "Time": "06:45PM",
      "msg": "Haliaeetus leucocephalus",
      "profile_pic": "assets/img/pic1.png",
      "account_name": "chainflux123"
    },
    {
      "Name": "Gowtham Varthan",
      "Time": "06:45PM",
      "msg": "Haliaeetus leucocephalus",
      "profile_pic": "assets/img/pic1.png",
      "account_name": "chainflux123"
    }
  ]
  i: any;

  constructor(private router:Router,private ms:MurmurService) { }

  myfunction(index)
  {
    var name=document.getElementsByClassName("h6")[index].textContent;
    this.ms.setdata(this.ChatUserList[index]);
    this.router.navigate(['/userchat']);

    document.getElementById(index).setAttribute(
      "style","border-left: 10px solid #a09fed;background: #171d2b;border-bottom: 1px solid #161c2b;");

    for( this.i=0; this.i<this.ChatUserList.length;this.i++)
    {
      if(this.i !== index)
      {
        document.getElementById(this.i).removeAttribute("style");    
      }
    }  
  }

  somevalue(text){
    if(text != ""){
     this.ms.getsearchProfile(text).then(res => {
        this.ChatUserList = res;
      })
    }
  }

  ngOnInit() {
  }

}
