import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-mainappone',
  templateUrl: './mainappone.component.html',
  styleUrls: ['./mainappone.component.css']
})
export class MainapponeComponent implements OnInit {
  My_Profile: any = {};
  login:boolean;
  
  constructor(private ms: MurmurService) { }

  ngOnInit() {
    setInterval(()=>{
      this.ms.Check_scatter().then(result => {
        if(this.login != result)
        {
          this.login = result
          if(result)
          {
            this.ms.getAccountProfile().then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
          }
          else
          {
            this.My_Profile = [];
          }
        }
      })
    },50)
  }

  otherUser()
  {
    this.ms.setOtherUserName(undefined)
  }

  logout()
  {
    this.ms.Scater_logout()      
  }

}
